body {
  font-family: "Muli", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Ubuntu, "Helvetica Neue", sans-serif;
  background: #f8fafc url("../images/bg.png") center top no-repeat;
  background-position-y: -999%;
  background-position-x: -999%;
}

body,
html,
#app {
  height: 100%;
}

input,
button {
  outline: 0;
}

ul {
  list-style: none;
}

#menu-desktop[data-id="tooltip"] {
  font-weight: 400;
  pointer-events: auto !important;
}

#menu-desktop[data-id="tooltip"].show {
  opacity: 1;
}

#menu-desktop[data-id="tooltip"]::after {
  border: 0 !important;
}

#menu.hide,
#menu.show {
  display: flex;
}

@media (max-width: 991.98px) {
  #menu.hide {
    animation: all 1s;
    opacity: 0;
    display: none !important;
    right: -100%;
  }

  #menu.show {
    display: flex !important;
    opacity: 1;
    width: 100% !important;
    height: 100%;
    position: fixed;
    right: 0;
    background: #fff;
    z-index: 2;
    color: #000;
    top: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  #menu.show::before {
    content: "";
    background: #000;
    width: 100%;
    height: 100%;
    position: absolute;
    left: -100%;
    opacity: 0.87;
  }

  #menu.show nav {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: auto;
  }

  #menu.show ul,
  #menu.show .actions {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  #menu.show .actions {
    max-width: 100%;
  }

  #menu.show ul {
    padding: 0;
  }

  #menu.show ul li {
    padding: 0.7rem 0;
  }
}
